import './App.css';
import { BrowserRouter as Router } from 'react-router-dom'
import Header from './components/Header';
import Home from './pages/Home';
import Footer from './components/Footer';
import Sidebar from './components/Sidebar';
import Content from './components/Content';
import Marketing from './pages/Marketing';
import AboutUs from './pages/AboutUs';
import Contact from './pages/Contact';
import PageNotFound from './pages/PageNotFound';
import WebDevelopment from './pages/WebDevelopment';



function App() {

  const pages = [
    { name: 'Kezdőlap', path: '/', menubar: true, element: <Home /> },
    { name: 'Online Marketing', path: '/marketing', menubar: true, element: <Marketing />},
    { name: 'Webfejlesztés', path: '/webdevelopment', menubar: true, element: <WebDevelopment/>},
    { name: 'Rólunk', path: '/about', menubar: true, element: <AboutUs/>},
    { name: 'Kapcsolat', path: '/contact', menubar: true, element: <Contact/>},
    { name: 'NotFound', path: '*', menubar: false, element: <PageNotFound/> }
  ];

  return (
    <div className='App'>
      <Router>
        <Header menu={pages}/>
        <Sidebar pageWrapId={'page-wrap'} outerContainerId={'outer-container'} />
        <Content routes={pages} />
        <Footer />
      </Router>
     
    </div>
  );
}

export default App;
