import '../styles/Footer.css'
import 'boxicons'
import Logo from '../assets/logo.png'

export default function Footer() {
    const socialMediaElements = [
        { name: "Facebook", href: "https://www.facebook.com/NRGUniqueSolutions/", icon: 'facebook' },
        { name: "Twitter", href: "https://www.linkedin.com", icon: 'linkedin-square' },
    ];

    const openInNewTab = url => {
        window.open(url, '_blank', 'noopener,noreferrer');
      };

    return (
        <div className="footer">
            <p>NRG Solutions &copy; 2022</p>
            <div className='socialMedia'>
                {socialMediaElements.map(element => (
                    <span key={element.name} style={{ cursor: 'pointer' }}
                    onClick={() => openInNewTab(element.href)}>
                        <box-icon type='logo' name={element.icon} color='#cbb26a' size='lg' animation='tada-hover'></box-icon>
                    </span>
                ))}
            </div>
            <img src={Logo} />
        </div>
    )
}