import "../styles/Contact.css"
import LeftPicture from '../assets/contact.jpg'
import CustomMotion from "../components/CustomMotion"

export default function Contact(props) {
    return (
        <CustomMotion className='contact'>

            <div className="contact-left"
                style={{ backgroundImage: `url(${LeftPicture})` }}
            ></div>
            <div className="contact-right">

                <h2>Kapcsolatfelvétel</h2>
                <form action="" method="post">
                    <label>Teljes név</label>
                    <input type="text" placeholder="Adja meg a nevét" autoComplete="off" />

                    <label>Email</label>
                    <input type="email" placeholder="Adja meg az email címét" autoComplete="off" />

                    <label>Telefonszám</label>
                    <input type="tel" id="phone" name="phone" pattern="[0-9]{3}-[0-9]{2}-[0-9]{3}" placeholder="Adja meg a telefonszámát" autoComplete="off" />

                    <label>Miben segíthetünk?</label>
                    <textarea rows={6} placeholder="Üzenet..."></textarea>

                    <div className="checkbox">
                        <input type="checkbox" className="checkbox-left" />
                        <div className="checkbox-right">Elolvastam és megértettem az NRG Solutions <a href='/adatkezelesi_tajekoztato.pdf' target="_blank">Adatkezelési Tájékoztatóját és Szabályzatát</a>, és hozzájárulok ahhoz, hogy a megadott adataimat a szolgáltató, mint adatkezelő a Szabályzatban foglaltaknak megfelelően kezelje.</div>
                    </div>
                    <div className="buttons">
                        <button>Küldés</button><a href="tel:06702067961">Hívjon most</a>
                    </div>
                </form>
            </div>
        </CustomMotion>
    )
}